	import moveVerify from "@/components/moveVerify.vue"
	import popup from '@/components/popup.vue'
	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		components: {
		        "move-verify":moveVerify,
				"popup": popup  
		},
		data(){
			return {
				mobile: '',
				password: '',
				verifyCode:'',
				loginName: '',
				nickName: '',
				logining: false,
				phoneExistance: false,
				nameExistance: false
			}
		},
		onLoad(){
			console.log('login页面onLoad');
		},
		methods: {
			...mapMutations(['login']),
			/* 校验结果回调函数 */
			verifyResult(res){
				console.log(res);
				this.resultData = res;
			},
			/* 校验插件重置 */
			verifyReset(){
				this.$refs.verifyElement.reset();
			
				/* 删除当前页面的数据 */
				this.resultData = {};
			},
			
			/* 协议弹窗状态 */
			popupState(e){ //e->false取消 true确认
				
			},
			navBack(){
				uni.navigateBack({
					delta: 1
				});
			},
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			toGuidance(){
				uni.navigateTo({
					url: '/pages/public/guidance',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toAgreement(){
				uni.navigateTo({
					url: '/pages/public/agreement',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			checkExistPhone(){
				if(this.$refs.verifyElement.phoneExistance == true){
					this.phoneExistance = true;
					this.$api.msg("该手机号已被注册");
				}else{
					this.$api.msg("验证码已发送");
					this.phoneExistance = false;
				}
			},
			checkExistName(){
				console.log(this.loginName);
				/* 这里判断数据库中是否存在该用户名 */
				uni.request({
					url: this.apiServer+'/customer/checkNameExistance',
					method: 'POST',
					dataType: "json",
					data: { 
					   "loginName": this.loginName,
					},
					success: (res) => {
						const result = res.data
						console.log(result)
						if(result != 0){
							this.nameExistance = true;
							this.$api.msg("该用户名已被占用")
						}else{
							this.nameExistance = false;
						}
				    }
				});
			},
			addCustomer(){
				if(this.nameExistance == false && this.phoneExistance == false){
					uni.request({
					   // url:'http://localhost:8888/api/getAll',
					   url: this.apiServer+'/customer/addNewCustomer',
					   method: 'POST',
					   dataType: "json",
					   data: { 
						 mobile: this.mobile,
						 password: this.password,
						 loginName: this.loginName,
						 nickName: this.nickName
					   },
					   success: (res) => {
							const result = res.data
							console.log(result)
					    }
					});
				}
			},
			async toRegister(){
				this.addCustomer();
				this.$api.msg("注册成功，即将跳转登录")
				setTimeout(() => {
					uni.navigateTo({
						url: '/pages/public/loginByPhone',
					});
				}, 3000)
				
			}
		},

	}
